import React, { Component } from "react";
import { Button } from 'antd';
import { connect } from 'react-redux';
import { langCheck } from "Pub/js/utils";
import { setTemplateNameVal } from 'Store/TemplateSetting/action';
import CopyComponent from '../TemplateSetting/copyMoudle';

class CopyMobileBtn extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showCopyModal: false, //显示复制弹窗
            selectedTemplate: [], //选中的模板数据
        };
    }

    //点击复制
    handleShowCopyModal = () => {
        this.props.onClick((record) => {
            this.props.setTemplateNameVal(record[0] && record[0].name);
            this.setState({ 
                showCopyModal: true,
                selectedTemplate: record
             });
        });
    }

    //关闭复制弹窗
    handleCopyVisible = visible => {
        this.setState({ showCopyModal: visible });
    }

    //复制模板
    handleCopy = (data) => {
        this.props.onCopy(data.data);
    }

    render() {
        const { appRegisterJson, json, selectedPage } = this.props;
        const { selectedTemplate } = this.state;
        const mobileInfoData = {
            templateType: 'bill',
            pageCode: selectedPage.pageCode,
            appCode: selectedTemplate[0] && selectedTemplate[0].appcode,
            templateId: selectedTemplate[0] && selectedTemplate[0].pk_page_templet
        }
        return <div>
            <Button onClick={this.handleShowCopyModal} style={{ marginLeft: "8px" }}>
                {langCheck("102202APP-000155", "pages", appRegisterJson)}
                {/* 国际化处理： 复制*/}
            </Button>
            {
                this.state.showCopyModal && 
                <CopyComponent
                    json={json}
                    visible={this.state.showCopyModal}
                    setCopyVisible={this.handleCopyVisible}
                    mobileInfoData={mobileInfoData}
                    onCopy={this.handleCopy}
                />
            }
        </div>
        
    }
}
export default connect(
    (state) => ({
        templateNameVal: state.TemplateSettingData.templateNameVal,
        nodeData: state.AppRegisterData.nodeData
    }),
    {
        setTemplateNameVal
    }
)(CopyMobileBtn);