import React, { Component } from "react";
import { connect } from "react-redux";
import AppRegister from '../AppRegister';
import CopyMobileBtn from './Copy';
import AssignBtn from './Assign';
import { getMulti } from 'Pub/js/getMulti';

class MobileAppRegister extends Component {
    constructor(props) {
        super(props);
        this.state = {
            json: {}, //模板设置多语
        };
    }

    componentDidMount() {
        let callback = json => {
            this.setState({ json });
        };
        getMulti({
            moduleId: "10180TM",
            domainName: "workbench",
            callback
        });
    }

    render(){
        const { langJson, selectedPage } = this.props;
        const commonProps = { 
            selectedPage, 
            appRegisterJson: langJson,
            json: this.state.json
        }
        const mobileItem = {
            copy: (pageTableProps) => <CopyMobileBtn {...pageTableProps} {...commonProps}/>,
            assign: (pageTableProps) => <AssignBtn {...pageTableProps} {...commonProps}/>
        }
        return <AppRegister 
            pageType="mobile"
            renderMobileItem={mobileItem}
        />
    }
}

export default connect(
    (state) => ({
        langJson: state.AppRegisterData.langJson,
        selectedPage: state.AppRegisterData.selectedPage
    }),
    { }
)(MobileAppRegister);