import React, { Component } from "react";
import { Button } from 'antd';
import { connect } from 'react-redux';
import AssignComponent from '../TemplateSetting/assignComponent';
import { langCheck } from "Pub/js/utils";

class AssignBtn extends Component {
    constructor(props) {
        super(props);
        this.state = {
            alloVisible: false, //显示分配弹窗
            selectedTemplate: [] //选中的模板数据
        };
    }

    showAssignModal = visibel => {
        this.props.onClick((selectedRows) => {
            this.setState({ 
                alloVisible: visibel,
                selectedTemplate: selectedRows
             });
        });
    }
    
    render(){
        const { appRegisterJson, json, selectedPage } = this.props;
        const { selectedTemplate } = this.state;
        const mobileInfoData = {
            templateType: 'bill',
            pageCode: selectedPage.pageCode,
            appCode: selectedTemplate[0] && selectedTemplate[0].appcode,
            templateId: selectedTemplate[0] && selectedTemplate[0].pk_page_templet
        }
        return <div>
            <Button onClick={() => this.showAssignModal(true)} style={{ marginLeft: "8px" }}>
                {langCheck("102202APP-000156", "pages", appRegisterJson)}
                {/* 国际化处理： 分配*/}
            </Button>
            {
                this.state.alloVisible && (
                    <AssignComponent
                        json={json}
                        alloVisible={this.state.alloVisible}
                        setAssignModalVisible={this.showAssignModal}
                        mobileInfoData={mobileInfoData}
                        {...this.props}
                    />
                )
            }
        </div>
    }
}
export default connect(
    (state) => ({
        selectedPage: state.AppRegisterData.selectedPage
    }),
    {}
)(AssignBtn);